import { Component, h, Prop, Element } from '@stencil/core';
import { cart } from '../../../global/data/cart';
import { easyprotectChange, easyprotectRemove } from '../../../global/functions/cart';
import priceFormat from '../../../global/functions/price';


@Component({
  tag: 'ks-cart-easyprotect-warranty',
  styleUrl: 'cart-easyprotect-warranty.css'
})
export class CartEasyprotectWarranty {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) productId: string;
    @Prop({reflect: true}) insured: boolean;
    @Prop({reflect: true, mutable: true}) active: string;

    @Prop({mutable: true}) name: string;
    @Prop({mutable: true}) time: number;
    @Prop({mutable: true}) price: number;

    entries: [string, number][];
    options: {[index: string]: number};

    componentWillLoad() {
        if(this.productId) {
            if(!this.active)
                this.active = Object.keys(cart.get("easyprotect")[this.productId])[0];

            this.update();
            cart.onChange("easyprotect", ()=>this.update());
        }
    }

    componentWillUpdate() {
        if(this.productId) {
            this.root.querySelector("select").value = this.active;
            this.update();
        }
    }

    update() {
        if(cart.get("easyprotect")[this.productId] === undefined)
            return;

        this.name = cart.get("products")[this.productId].name;
        this.options = cart.get("easyprotect")[this.productId];
        this.entries = Object.entries(this.options);
        this.price = this.options[this.active];
    }

    render() {
        const price = priceFormat(this.price);

        return [
            <div class="name" innerHTML={this.name.replace("<br>", " ")}
                 title={this.name.replace("<br>", " ").replace(/(<([^>]+)>)/gi, "")}
            ></div>,
            
            <div class="control">
                <div class="info">
                    <div class="select">
                        {this.productId ? [
                            <ks-input-select onChange={() => this.change()}>
                                {this.entries.map(([months, _]) =>
                                    <option value={months} selected={this.active == months}>
                                        {this.months(parseInt(months))}
                                    </option>    
                                )}
                            </ks-input-select>,
                            <ks-icon name="chevron-down"/>
                        ] :
                            this.months(this.time)
                        }
                    </div>
                    <div class="price">{price}</div>
                </div>

                {this.productId && this.insured ? 
                    <button class="close" onClick={()=>this.remove()}>
                        <ks-icon name="x"/>
                    </button> 
                : null}
            </div>,
        ];
    }

    change() {
        this.active = this.root.querySelector("select").value;
        if(this.insured) easyprotectChange({[this.productId]: this.active});
    }

    remove() {
        easyprotectRemove(this.productId);
    }

    months(x: number) {
        const digit = parseInt(`${x}`[`${x}`.length-1]);

        let month = "miesięcy";
        if(x == 0) month = "miesiąc" 
        else if(x >= 5 && x <= 21) month = "miesięcy" 
        else if(x > 21 && digit >= 2 && digit <= 4) month ="miesiące"

        return `${x} ${month}`;
    }
}