import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-cart-easyprotect-product',
  styleUrl: 'cart-easyprotect-product.css'
})
export class CartEasyprotectProduct {
    @Prop() image: string;
    @Prop() name: string;
    @Prop() warranty: string = "";
    @Prop({reflect: true, mutable: true}) active: boolean = false;

    render() {
        return [
            <div class="image">
                <ks-img2 src={this.image} width={200} height={200} />
            </div>,
            <div class="info">
                <div class="name" innerHTML={this.name}></div>
                {this.warranty ? 
                    <div class="warranty">{this.warranty}</div>
                : null}
            </div>
        ]
    }
}